import { mapMutations } from 'vuex';

export default {
	data() {
		return {
			loading: false
		};
	},
	methods: {
		...mapMutations(['login']),
		async toLogin() {
			console.log('toLogin');
			this.logining = true;
			this.login();
		},

		getUserInfo() {
			let _this = this;

			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: login => {
								uni.getUserInfo({
									provider: 'weixin',
									success: info => {
										console.log(login.code);
										_this.login({
											grant_type: 'password',
											client_id: 'youlai-mall-weapp',
											client_secret: '123456',
											code: login.code,
											encryptedData: info.encryptedData,
											iv: info.iv,
											userInfo: info.userInfo
										});
									},
									fail: () => {
										uni.showToast({
											title: '微信登录授权失败',
											icon: 'none'
										});
									}
								});
							},
							fail: () => {
								uni.showToast({
									title: '微信登录授权失败',
									icon: 'none'
								});
							}
						});
					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: 'none'
						});
					}
				}
			});

		}
	}
};
